.logoDiagnol >  .video {
    
    -webkit-animation: diagonal 2s;
    animation: diagonal 2s;
    }
    
    @-webkit-keyframes diagonal {
        0%       { -webkit-transform: matrix(1,0,0,1,0,0); transform: matrix(1,0,0,1,0,0) }
        100%     { -webkit-transform: scale(0.1) translatex(-400%) translatey(-5000px); transform: scale(0.1) translatex(-400%) translatey(-5000px) }
    }
    
    @keyframes diagonal {
        0%       { -webkit-transform: matrix(1,0,0,1,0,0); transform: matrix(1,0,0,1,0,0) }
        100%     {-webkit-transform: scale(0.1) translatex(-400%) translatey(-5000px);transform: scale(0.1) translatex(-400%) translatey(-5000px) }
    }


